<template>
  <div class="ratio-parent" v-if="useRaitoSizing">
    <div class="ratio-child">
      <div class="container-fluid w-100 h-100 overflow-hidden position-relative" :style="mainBodyStyle">
           <transition
           :name="anime" 
           mode="out-in"
     >
     <InlineCalender
          v-if="level==0" 
          :value="monthSetter"
          @inputVal="getMainVal"
          v-bind="(!!inlineStyle)?inlineStyle:{}"
          @getCurrentYear="getCurrentYear" 
          @clickTitle="clickTitle"
          />
          <MonthCalender 
          v-else-if="level==1"
          v-bind="(!!monthStyle)?monthStyle:{}" 
          :value="year"
          @getCurrentYear="getCurrentYearMonth"
          @selectedMonth="selectedMonth" 
          @clickTitleMonth="clickTitleMonth"
          />
          <YearCalender 
          v-else-if="level==2" 
          
          v-bind="(!!yearStyle)?yearStyle:{}" 
          :value="tenYear"
          @selectedYear="selectedYear" 
          @clickTitleYear="clickTitleYear"
          />
        
        <TenYearCalender 
        v-else 
          v-bind="(!!tenYearStyle)?tenYearStyle:{}" 
        :value="year" 
        @selectedTenYear="selectedTenYear"/>
           </transition>
         
        
      </div>
    </div>
  </div>

  <div class="container-fluid" :style="mainBodyStyle" v-else>
  <transition
           :name="anime" 
           mode="out-in"
     >
     <InlineCalender
          v-if="level==0" 
          @inputVal="getMainVal"
          :value="monthSetter"
          v-bind="(!!inlineStyle)?inlineStyle:{}"
          @getCurrentYear="getCurrentYear" 
          @clickTitle="clickTitle"
          />
          <MonthCalender 
          v-else-if="level==1"
          v-bind="(!!monthStyle)?monthStyle:{}" 
          :value="year"
          @getCurrentYear="getCurrentYearMonth"
          @selectedMonth="selectedMonth" 
          @clickTitleMonth="clickTitleMonth"
          />
          <YearCalender 
          v-else-if="level==2" 
          
          v-bind="(!!yearStyle)?yearStyle:{}" 
          :value="tenYear"
          @selectedYear="selectedYear" 
          @clickTitleYear="clickTitleYear"
          />
        
        <TenYearCalender 
        v-else 
          v-bind="(!!tenYearStyle)?tenYearStyle:{}" 
        :value="year" 
        @selectedTenYear="selectedTenYear"/>
           </transition>
  </div>
</template>

<script>
import TenYearCalender from "./TenYearCalender";
import YearCalender from "./YearCalender";
import MonthCalender from "./MonthCalender";
import InlineCalender from "./InlineCalender";
export default {
  props: {
      animUp:{
        type:String,
        default:"bounce"
      },
      animDown:{
type:String,
        default:"bounce-out"
      },
      yearStyle:{
          type:Object,
          default:null
      },
      inlineStyle:{
          type:Object,
          default:null
          },
      monthStyle:{type:Object,
          default:null},
      tenYearStyle:{
type:Object,
          default:null
      },
    useRaitoSizing: {
      type: Boolean,
      default: true
    },
    mainBodyStyle: {
      type: Object,
      default() {
        return {
        //   background: "#0a56cc",
          // margin: "auto",
        //   border: "2px solid #eee",
        //   borderRadius: "15px",
        //   padding: "15px"
        };
      }
    }
  },
  methods:{
      clickTitle(){
          this.anime=this.animUp
          this.level=1
      },
      clickTitleYear(){
          this.anime=this.animUp
          this.level=3
        //   ////////console.log('object')
      },
      clickTitleMonth(){
          this.anime=this.animUp
          this.level=2
          
          ////////console.log('title cilik')
      },getMainVal(val){
          this.$emit('input',val)
      }
      ,getCurrentYearMonth(val){
          this.tenYear=val
          
          this.anime="this.animDown"
        this.level=1;
      },
      selectedYear(val){
          this.year=val
          
          this.anime=this.animDown
        this.level=1;
      },selectedMonth(val){
          this.month=val
          this.anime=this.animDown
          ////////console.log(val," ffffffsagwgh")
        this.level=0
      },selectedTenYear(val){
          this.tenYear=val
          
          this.anime=this.animDown
        this.level=2
      },getCurrentYear(val){
          
          this.year=val
        //   ////////console.log('ffffffffffaga')
      }
  },
  data() {
    return {
        level:0,
        month:"",
        day:"",
        tenYear:"",
        year:"",
        anime:this.animUp
    };
  },
  computed:{
      monthSetter(){
          if(this.month.length==0){
              return ""
          }else if(this.month.length>=8){
              return this.month
          }else{
              return this.month+'-01'
          }
      }
  },
  components: {
    TenYearCalender,
    YearCalender,
    MonthCalender,
    InlineCalender
  }
};
</script>

<style>
.ratio-parent {
  position: relative;
  padding-top: 100%;
  width: 100%;
}
.ratio-child {
  position: absolute;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade-enter-to,.fade-leave{
    opacity: 1;
}

.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateX(10px);
  opacity: 0;
}


.tran{
    transform: translateY(-100%)
}
/* .bounce-enter-active {
  animation: bounce-in 1s;
} */
.bounce-leave-active {
  animation: bounce-in 0.5s;
}
.bounce-out-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
      position: relative;
    transform: scale(1);
  }
  100% {
      position: absolute;
    transform: scale(5.5);
  }
  }
</style>
