<template>
  <div class="ratio-parent" v-if="useRaitoSizing">
    <div class="ratio-child">
      <div class="container-fluid w-100 h-100" :style="mainBodyStyle">
        <div
          v-if="showTitle"
          class="row no-gutters"
          :style="Object.assign(titleStyle, { height: raitoTitle * 10 + '%' })"
        >
          <div
            class="d-flex justify-content-center align-items-center w-100 h-100"
          >
            <div
              class="btn btn-small"
              style="cursor: pointer"
              @click="perMonth"
            >
              <svg
                id="i-chevron-left"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 32 32"
                width="18"
                height="16"
                fill="none"
                stroke="currentcolor"
                stroke-linecap="round"
                stroke-linejoin="round"
                :style="arrowLeftStyle"
                stroke-width="7.8125%"
              >
                <path d="M20 30 L8 16 20 2" />
              </svg>
            </div>
            <div class="d-flex justify-content-around" 
              @click="$emit('clickTitle')"
              :style="Object.assign({ minWidth: '20%' }, clickableStyle)">
              <div>{{ currYear | convertEnToFaDig }} -</div>

              <transition :name="monthAnim">
                <div v-if="changeAnim">{{ currMonth }}</div>
              </transition>
            </div>
            <div
              class="btn btn-small danger"
              style="cursor: pointer"
              @click="nextMonth"
            >
              <svg
                id="i-chevron-right"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 32 32"
                width="18"
                height="16"
                fill="none"
                stroke="currentcolor"
                stroke-linecap="round"
                stroke-linejoin="round"
                :style="arrowRightStyle"
                stroke-width="7.8125%"
              >
                <path d="M12 30 L24 16 12 2" />
              </svg>
            </div>
          </div>
        </div>
        <div
          class="d-flex w-100 flex-row-reverse overflow-hidden"
          :style="{ height: raitoBody * 10 + '%' }"
        >
          <div
            class="d-flex flex-column justify-content-between  flex-nowrap  overflow-hidden"
            :style="Object.assign(headerCalenderStyle)"
          >
            <div
              class="d-flex justify-content-end  align-items-center"
              :style="headerCalenderItemStyle"
              v-for="k in header"
              :key="k + 100 + 100 + ';'"
            >
              {{ k }}
            </div>
          </div>

          <div
            class="d-flex flex-column  flex-nowrap "
            v-for="j in totalMainRow"
            :key="'jj' + j"
            :style="Object.assign(rowCalenderStyle)"
          >
            <div class="col position-relative" v-for="i in 7" :key="'h' + i">
              <transition :name="calenderItemAnim">
                <div
                  @click="selecValues(firstRowLastDays + i, $event)"
                  class="ratio-child"
                  :style="
                    Object.assign(
                      styleCellCal1(firstRowLastDays + i),
                      calenderCellWithTextStyle
                    )
                  "
                  v-if="
                    j == 1 &&
                      endDay > firstRowLastDays &&
                      firstRowLastDays + i <= endDay &&
                      isFiveRow
                  "
                >
                  <div
                    class="d-flex justify-content-center align-items-center h-100 w-100"
                  >
                    {{ (firstRowLastDays + i) | convertEnToFaDig }}
                  </div>
                </div>
                <div
                  @click="selecValues(i - startDay, $event)"
                  class="ratio-child"
                  :style="
                    Object.assign(
                      styleCellCal1(i - startDay),
                      calenderCellWithTextStyle
                    )
                  "
                  v-else-if="j == 1 && i - 1 >= startDay"
                >
                  <div
                    class="d-flex justify-content-center align-items-center h-100 w-100"
                  >
                    {{ (i - startDay) | convertEnToFaDig }}
                  </div>
                </div>
                <div v-else-if="j == 1" :style="calenderCellNoTextStyle"></div>
                <div
                  @click="selecValues((j - 1) * 7 + i - startDay, $event)"
                  class="ratio-child"
                  :style="
                    Object.assign(
                      styleCellCal1((j - 1) * 7 + i - startDay),
                      calenderCellWithTextStyle
                    )
                  "
                  v-else-if="j > 1 && (j - 1) * 7 + i - startDay <= endDay"
                >
                  <div
                    class="d-flex justify-content-center align-items-center h-100 w-100"
                  >
                    {{ ((j - 1) * 7 + i - startDay) | convertEnToFaDig }}
                  </div>
                </div>
                <div v-else :style="calenderCellNoTextStyle"></div>
              </transition>
            </div>
          </div>
        </div>

        <div
          v-if="showFooter"
          class="row no-gutters"
          :style="
            Object.assign(footerStyle, { height: raitoFooter * 10 + '%' })
          "
        >
          <div class="d-flex justify-content-center w-100">
            <div
              v-if="showButtonToday"
              class="btn btn-small"
              :style="Object.assign(todayButtonStyle, { cursor: 'pointer' })"
              @click="goToday"
            >
              {{ todayButtonTitle }}
            </div>
            <div v-if="showSelectedValue" :style="showSelectedValueStyle">
              {{ inputValue }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div class="container-fluid" :style="mainBodyStyle" v-else>
    <div v-if="showTitle" class="row no-gutters" :style="titleStyle">
      <div class="d-flex justify-content-center align-items-center w-100 h-100">
        <div class="btn btn-small" style="cursor: pointer" @click="perMonth">
          <svg
            id="i-chevron-left"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 32 32"
            width="18"
            height="16"
            fill="none"
            stroke="currentcolor"
            stroke-linecap="round"
            stroke-linejoin="round"
            :style="arrowLeftStyle"
            stroke-width="7.8125%"
          >
            <path d="M20 30 L8 16 20 2" />
          </svg>
        </div>
        <div class="d-flex justify-content-around"
          @click="$emit('clickTitle')"
          :style="Object.assign({ minWidth: '20%' }, clickableStyle)">
          <div>{{ currYear }} -</div>

          <transition :name="monthAnim">
            <div v-if="changeAnim">{{ currMonth }}</div>
          </transition>
        </div>
        <div
          class="btn btn-small danger"
          style="cursor: pointer"
          @click="nextMonth"
        >
          <svg
            id="i-chevron-right"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 32 32"
            width="18"
            height="16"
            fill="none"
            stroke="currentcolor"
            stroke-linecap="round"
            stroke-linejoin="round"
            :style="arrowRightStyle"
            stroke-width="7.8125%"
          >
            <path d="M12 30 L24 16 12 2" />
          </svg>
        </div>
      </div>
    </div>
    <div class="d-flex w-100 flex-row-reverse overflow-hidden">
      <div
        class="d-flex flex-column justify-content-between  flex-nowrap  overflow-hidden"
        :style="Object.assign(headerCalenderStyle)"
      >
        <div
          class="d-flex justify-content-end align-items-center"
          :style="headerCalenderItemStyle"
          v-for="k in header"
          :key="k + 100 + 100 + ';'"
        >
          {{ k }}
        </div>
      </div>

      <div
        class="d-flex flex-column  flex-nowrap "
        v-for="j in totalMainRow"
        :key="'jj' + j"
        :style="Object.assign(rowCalenderStyle)"
      >
        <div class="col position-relative" v-for="i in 7" :key="'h' + i">
          <transition :name="calenderItemAnim">
            <div
              @click="selecValues(firstRowLastDays + i, $event)"
              class="ratio-child"
              :style="
                Object.assign(
                  styleCellCal1(firstRowLastDays + i),
                  calenderCellWithTextStyle
                )
              "
              v-if="
                j == 1 &&
                  endDay > firstRowLastDays &&
                  firstRowLastDays + i <= endDay &&
                  isFiveRow
              "
            >
              <div
                class="d-flex justify-content-center align-items-center h-100 w-100"
              >
                {{ (firstRowLastDays + i) | convertEnToFaDig }}
              </div>
            </div>
            <div
              @click="selecValues(i - startDay, $event)"
              class="ratio-child"
              :style="
                Object.assign(
                  styleCellCal1(i - startDay),
                  calenderCellWithTextStyle
                )
              "
              v-else-if="j == 1 && i - 1 >= startDay"
            >
              <div
                class="d-flex justify-content-center align-items-center h-100 w-100"
              >
                {{ (i - startDay) | convertEnToFaDig }}
              </div>
            </div>
            <div v-else-if="j == 1" :style="calenderCellNoTextStyle"></div>
            <div
              @click="selecValues((j - 1) * 7 + i - startDay, $event)"
              class="ratio-child"
              :style="
                Object.assign(
                  styleCellCal1((j - 1) * 7 + i - startDay),
                  calenderCellWithTextStyle
                )
              "
              v-else-if="j > 1 && (j - 1) * 7 + i - startDay <= endDay"
            >
              <div
                class="d-flex justify-content-center align-items-center h-100 w-100"
              >
                {{ ((j - 1) * 7 + i - startDay) | convertEnToFaDig }}
              </div>
            </div>
            <div v-else :style="calenderCellNoTextStyle"></div>
          </transition>
        </div>
      </div>
    </div>
    <div v-if="showFooter" class="row no-gutters" :style="footerStyle">
      <div class="d-flex justify-content-center w-100">
        <div
          v-if="showButtonToday"
          class="btn btn-small"
          :style="Object.assign(todayButtonStyle, { cursor: 'pointer' })"
          @click="goToday"
        >
          {{ todayButtonTitle }}
        </div>
        <div v-if="showSelectedValue" :style="showSelectedValueStyle">
          {{ inputValue }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment-jalaali";

import fa from "moment/src/locale/fa";
export default {
  filters: {
    convertEnToFaDig: function(val) {
      let text = val + "";
      if (text.length == 0) {
        return "";
      }
      var ns = "۰۱۲۳۴۵۶۷۸۹";
      let out = "";
      let length = text.length;
      for (let i = 0; i < length; i++) {
        let c = text.charAt(i);
        let nump = parseInt(c);
        if (nump >= 0) {
          out += ns.charAt(nump);
        } else {
          out += c;
        }
      }
      return out;
    }
  },
  props: {
    actionStep: {
      type: Number,
      default: 1
    },
    moveAction: {
      type: Object,
      default() {
        return {
          action: "",
          step: 1,
          useJump: false,
          jump: {}
        };
      }
    },
    isFiveRow: {
      type: Boolean,
      default: false
    },
    value: {
      type: String,
      default: ""
    },
    selectedClassName: {
      type: String,
      default: "selected"
    },
    todayStyle: {
      type: Object,
      default() {
        return {
          color: "#e84545",
          background: "#00adb5",

          boxShadow: "0 0 5px #e84545",
          transition: " all 1s"
        };
      }
    },
    afterTodayStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee",

          boxShadow: "inset 0 0 10px #eeeeee",
          background: "#222831",
          transition: "all 1s"
        };
      }
    },
    beforeTodayStyle: {
      type: Object,
      default() {
        return {
          background: "#eeeeee",

          boxShadow: "inset 0 0 10px #222831",
          color: "#222831",
          transition: "all 1s"
        };
      }
    },
    selectStyle: {
      type: Object,
      default() {
        return {
          background: "orangered",

          transition: "all 1s"
        };
      }
    },
    calenderCellBodyStyle: {
      type: Object,
      default() {
        return {
          // background: "#000",
          // color: "#fff",
          // margin: "auto"
        };
      }
    },
    calenderCellWithTextStyle: {
      type: Object,
      default() {
        return {
          borderRadius: "50%",
          userSelect: "none",
          margin: "2px",
          "-webkit-user-select": "none",
          "-moz-user-select": "none",
          "-ms-user-select": "none",
          cursor: "pointer",

          transition: "all 1s"
        };
      }
    },
    calenderCellNoTextStyle: {
      type: Object,
      default() {
        return {
          background: "#000",
          color: "#fff",
          margin: "auto"
        };
      }
    },
    rowCalenderItemStyle: {
      type: Object,
      default() {
        return {
          // background: "#000",
          // color: "#fff",
          // margin: "auto"
        };
      }
    },
    rowCalenderStyle: {
      type: Object,
      default() {
        return {
          // margin:"0 -14px"
          // borderBottom:"0.5px solid #eeeeee"
        };
      }
    },
    headerCalenderItemStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee",
          margin: "0px 5px"
        };
      }
    },
    mainBodyStyle: {
      type: Object,
      default() {
        return {
          background: "#393e46",
          // margin: "auto",
          border: "2px solid #eee",
          borderRadius: "15px",
          padding: "15px"
        };
      }
    },
    arrowRightStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee"
        };
      }
    },

    arrowLeftStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee"
        };
      }
    },
    clickableStyle: {
      type: Object,
      default() {
        return {
          cursor: "pointer",
          userSelect: "none",
          "-webkit-user-select": "none",
          "-moz-user-select": "none",
          "-ms-user-select": "none",
          // marginBottom: "-14px",
        };
      }
    },
    titleStyle: {
      type: Object,
      default() {
        return {
          marginTop: "-10px",
          background: "#393e46",
          color: "#eeeeee"
        };
      }
    },
    headerCalenderStyle: {
      type: Object,
      default() {
        return {
          background: "#222831"
          // margin:"0 -14px"
        };
      }
    },
    footerStyle: {
      type: Object,
      default() {
        return {
          marginTop: "14px"
          // marginBottom: "-14px",
        };
      }
    },
    todayButtonStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee",
          outline: "none !important"
          // background: "#222831"
        };
      }
    },
    showSelectedValueStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee",
          display: "flex",
          alignItems: "center"
          // background: "#222831"
        };
      }
    },
    customFormat: {
      type: String,
      default: "jYYYY/jM/jD"
    },
    useRaitoSizing: {
      type: Boolean,
      default: true
    },
    raitoTitle: {
      type: Number,
      default: 1
    },
    raitoBody: {
      type: Number,
      default: 8
    },
    raitoFooter: {
      type: Number,
      default: 1
    },
    showTitle: {
      type: Boolean,
      default: true
    },
    showFooter: {
      type: Boolean,
      default: true
    },
    showButtonToday: {
      type: Boolean,
      default: true
    },
    showSelectedValue: {
      type: Boolean,
      default: true
    },
    calenderItemAnim: {
      type: String,
      default: "slide-fade"
    },
    monthAnim: {
      type: String,
      default: "fade"
    },
    todayButtonTitle: {
      type: String,
      default: "امروز"
    }
  },
  mounted() {
    this.momentObject = moment();
    this.momentObject.locale("fa", fa);
    this.today = this.conv(this.momentObject.format("YYYY-M-D"));
    this.defFormat = this.momentObject.format("jYYYY/jM/");
    this.todayDay = parseInt(this.conv(this.momentObject.format("jD")));
    this.todayMonth = this.monthHeader[
      this.conv(this.momentObject.format("jM"))
    ];
    this.todayYear = this.momentObject.format("jYYYY");
    if (typeof this.value !== "undefined" && this.value.length > 0) {
      
      let text = this.value;
      var ns = "۰۱۲۳۴۵۶۷۸۹";
      let out = "";
      let length = text.length;
      for (let i = 0; i < length; i++) {
        let c = ns.indexOf(text.charAt(i));
        let nump = parseInt(c);
        if (nump >= 0) {
          out += c;
        } else {
          out += text.charAt(i);
        }
      }
      //////////////////////console.log("object")

      //////////////////////console.log(out)
      let ddd = moment(out, "jYYYY/jM/jD");
     
  ddd.locale("fa", fa);
      //////////////////////console.log(ddd.format('jYYYY') )
      //////////////////////console.log(text)

      this.selectedMonth = this.monthHeader[this.conv(ddd.format("jM"))];

      // ddd.format("jM");
      this.selectedYear = ddd.format("jYYYY");

      this.selectedDay = ddd.format("jD");
      this.momentObject=ddd
      // console.log('rewtwet3    ',this.value,"out"+out," ",this.selectedYear)
      /////////////////console.log(this.selectedMonth)
      /////////////////console.log(this.selectedYear)
      /////////////////console.log(ddd.format("jD"))
      //   if (this.value != null&& this.value.length>0) {
      //   this.lastSelectedNode.classList.remove(this.selectedClassName);
      //   if (
      //     this.selectedYear == this.momentObject.format("jYYYY") &&
      //     this.selectedMonth == this.momentObject.format("jM")
      //   ) {
      //     this.lastSelectedNode.classList.add(this.selectedClassName);
      //   } else {
      //     this.lastSelectedNode.classList.remove(this.selectedClassName);
      //   }
      // }
      // /////////////////console.log(ddd.jYear())
      // this.ddd.locale("fa", fa);
      // /////////////////console.log(ddd)
      /////////////////console.log('object')
    }
    // moments.add(2, "jMonth");
    this.changeMonth(true);

    // this.momentObject.startOf("jMonth");
    // ///////////////////console.log(this.momentObject.format("jYYYY/jM/jD"));
    // this.startDayMoment = this.momentObject;
    // ///////////////////console.log(this.startDayMoment.format("jYYYY/jM/jD"));
    // let a = "۰۱۲۳۴۵۶۷۸۹";
    // this.startDay = parseInt(a.indexOf(this.momentObject.format("e")));

    //   this.firstRowLastDays=(4*7)+(7-(this.startDay))
    // /////////////console.log((4*7)+(7-(this.startDay))," ",this.startDay)

    // this.momentObject.endOf("jMonth");
    // this.endDay = parseInt(this.conv(this.momentObject.format("jD")));
    // this.currMonth = this.monthHeader[
    //   this.conv(this.momentObject.format("jM"))
    // ];
    // this.currYear = this.momentObject.format("jYYYY");

    // ///////////////////console.log(this.conv(moments.format("jD")));
    // ///////////////////console.log(this.startDay);
    // ///////////////////console.log(this.endDay);
    // // moments
    // /////////////////console.log(moments.format("jYYYY/jM/jD dd dddd e"));
    // /////////////////console.log(moments.startOf("jMonth").format("jYYYY/jM/jD"));
    // /////////////////console.log(moments.endOf("jMonth").format("jYYYY/jM/jD HH:mm:ss"));
    // moments.add(5, "jMonth");
    // /////////////////console.log(moments.format("jYYYY/jM/jD"));
    // /////////////////console.log(moments.endOf("jMonth").format("jYYYY/jM/jD jHH:mm:ssD"));
  },
  data() {
    return {
      // isViseble:[true,true,true,true,true,true],
      totalMainRow: this.isFiveRow ? 5 : 6,
      firstRowLastDays: 29,
      header: {
        0: "شنبه",
        1: "یکشنبه",
        2: "دوشنبه",
        3: "سه‌شنبه",
        4: "چهارشنبه",
        5: "پنجشنبه",
        6: "جمعه"
      },
      monthHeader: {
        1: "فروردین",
        2: "اردیبهشت",
        3: "خرداد",
        4: "تیر",
        5: "مرداد",
        6: "شهریور",
        7: "مهر",
        8: "آبان",
        9: "آذر",
        10: "دی",
        11: "بهمن",
        12: "اسفند"
      },
      defFormat: "jYYYY-jM-",
      startDayMoment: null,
      changeAnim: true,
      currYear: "",
      today: "",
      todayDay: 1,
      todayMonth: "",
      todayYear: "",
      selectedYear: "",
      selectedMonth: "",
      selectedDay: "",
      currMonth: "",
      startDayisBeforeToday: false,
      momentObject: null,
      startDay: 1,
      endDay: 29,
      sDay: 0,
      inputValue: "",
      lastSelectedNode: null,
      lastSelectedNodeClass: ""
    };
  },
  watch: {
    currMonth(val) {
      this.$emit("getCurrentMonth", val);
    },
    currYear(val) {
      this.$emit("getCurrentYear", val);
    },
    moveAction(val) {
      /////////console.log(this.actionStep," s ")
      /////////console.log(typeof val)
      /////////console.log(val.useJump)
      /////////console.log(typeof val.useJump==='undefined')
      /////////console.log(val.useJump === null)
      if (
        typeof val === "object" &&
        val !== null &&
        typeof val.action !== "undefined" &&
        val.action !== null &&
        (typeof val.useJump === "undefined" ||
          val.useJump === null ||
          !val.useJump)
      ) {
        let v = val.action.toLowerCase();
        if (
          v == "n" ||
          v == "ne" ||
          v == "nex" ||
          v == "next" ||
          v.indexOf("n") == 0 ||
          v.indexOf("ne") == 0 ||
          v.indexOf("nex") == 0 ||
          v.indexOf("next") == 0
        ) {
          this.nextMonth();
        } else if (
          v == "previous" ||
          v == "previou" ||
          v == "previo" ||
          v == "previ" ||
          v == "prev" ||
          v == "pre" ||
          v == "pr" ||
          v == "p" ||
          v.indexOf("previous") == 0 ||
          v.indexOf("previou") == 0 ||
          v.indexOf("previo") == 0 ||
          v.indexOf("previ") == 0 ||
          v.indexOf("prev") == 0 ||
          v.indexOf("pre") == 0 ||
          v.indexOf("pr") == 0 ||
          v.indexOf("p") == 0
        ) {
          this.perMonth();
        } else if (
          v == "today" ||
          v == "toda" ||
          v == "tod" ||
          v == "to" ||
          v == "t" ||
          v.indexOf("today") == 0 ||
          v.indexOf("toda") == 0 ||
          v.indexOf("tod") == 0 ||
          v.indexOf("to") == 0 ||
          v.indexOf("t") == 0
        ) {
          this.goToday();
        }
      } else {
        if (
          typeof val.useJump !== "undefined" &&
          val.useJump !== null &&
          val.useJump &&
          typeof val.jump === "object" &&
          val.jump !== null
        ) {
          // if(val.jump.year=)
          /////////console.log("object")
          /////////console.log(this.momentObject.format('jYYYY/jM/jD'))
          if (typeof val.jump.year !== "undefined" && val.jump.year !== null) {
