            this.momentObject.add(val.jump.month, "jMonth");
          }
          if (typeof val.jump.day !== "undefined" && val.jump.day !== null) {
            this.momentObject.add(val.jump.day, "day");
          }
          this.changeMonth();
          /////////console.log(this.momentObject.format('jYYYY/jM/jD'))
        }
      }
