      ///////////////////console.log(this.selectedDay,"jjjjjjjjj",index," ",this.selectedDay == index,this.currMonth == this.selectedMonth,this.selectedYear == this.conv(this.currYear),this.currYear,this.selectedYear)
      if (
        this.selectedYear == this.conv(this.currYear) &&
        this.currMonth == this.selectedMonth &&
        this.selectedDay == index
      ) {
        ///////////////////console.log(this.selectedDay,"mmmmmmmmmmmmmmmmmmmmmmm")
        return this.selectStyle;
      } else {
        if (
          this.currYear == this.todayYear &&
          this.currMonth == this.todayMonth
        ) {
          if (this.todayDay == index) {
            return this.todayStyle;
          } else if (this.todayDay >= index) {
            return this.beforeTodayStyle;
          } else {
            return this.afterTodayStyle;
          }
        } else if (this.startDayisBeforeToday) {
          return this.beforeTodayStyle;
        } else {
          return this.afterTodayStyle;
        }
      }
