    setDater() {
      /////////////////////console.log(this.sDay);
      this.sDay = this.sDay + 1;
      return this.sDay;
    },
    conv(val) {
      let text = val + "";
      if (text.length == 0) {
        return "";
      }
      var ns = "۰۱۲۳۴۵۶۷۸۹";
      let out = "";
      let length = text.length;
      for (let i = 0; i < length; i++) {
        let c = text.charAt(i);
        let nump = ns.indexOf(c + "");
        if (nump >= 0) {
          out += nump;
        } else {
          out += c;
        }
      }
      return out;
    }
